.main{
    &__graph{
        width: 80%;
        margin: 0 auto;
        margin-top: 100px;
        h3{
            text-align: center;
            font-size: 2rem;
        }
        img{
            width: 100%;
        }
    }
    @media(max-width:991px){
        &__graph{
            width: 90%;
        }
    }
    @media(max-width:767px){
        &__graph{
            width: 100%;
            margin-top: 75px;
        }
    }
}